import React from 'react';
import styled from 'styled-components';
import LogoFFF from '../images/footer/logo_fff.svg';
import LogoSiS from '../images/footer/logo_sis_blanco.png';

const Footer = () => (
    <FooterWrapper>
        <div className="logoContainer">
            <img src={LogoFFF} alt="FFF" />
        </div>
        <div className="container">
            <div className="linkSite">
                <div><a href="#presentation">Presentation</a></div>
                <div><a href="#categories">Categories</a></div>
                <div><a href="#whyparticipate">Why participate?</a></div>
                <div><a href="#apply">Apply</a></div>
                <div><a href="#deadline">Deadline</a></div>
                <div><a href="#prizes">Prizes</a></div>
                <div>Jury</div>
                <div>Partners</div>
                <div><a href="#about">About</a></div>
                <div><a href="#ticketing">Ticketing</a></div>                
            </div>
            <div className="contact">
                <div className="mail">
                    <div className="mailTitle">Contact</div>
                    <div className="mailText"><a href="mailto:fffinnovationawards@fff.fr">fffinnovationawards@fff.fr</a></div>
                </div>
                <div className="bottomLine">
                    <div className="newsletterBtn">Signup to Newsletter</div>
                    <div className="bottomLogoContainer">
                        <img src={LogoSiS} alt="SiS" />
                    </div>
                </div>

            </div>
        </div>
    </FooterWrapper>
);

export default Footer;

const FooterWrapper = styled.div`
    position: relative;
    background-color: #004586;
    padding-top: 80px;
    color: #ffffff;
    font-family: 'Gotham Condensed', sans-serif;

    .logoContainer{
        width: 109px;
        height: 117px;
        position: absolute;
        left: 0px;
        right: 0px;
        top: -60px;
        margin: auto;
    }

    .container{
        width: 90%;
        margin: auto;
        display: grid;
        grid-template-columns: 100%; 
        grid-template-rows: 180px 200px;
        grid-gap: 30px 10px;
    }

    .linkSite{
        display: grid;
        grid-template-columns: 45% 45%; 
        grid-template-rows: 20px 20px 20px 20px 20px;
        grid-gap: 10%;
    }

    .contact{
        display: grid;
        grid-template-columns: 100%;
        grid-template-rows: 40px 90px;
        grid-gap: 10px 10px;
    }

    .mailTitle{font-family: 'Bebas Neue', sans-serif;}

    .newsletterBtn{
        font-family: 'Bebas Neue', sans-serif;
        width: 80%;
        margin: 0px auto 30px;
        background-color: #B79257;
        color: #ffffff;
        padding: 15px 0px;
        cursor: pointer;
        text-align: center;
    }

    .mailText a{
        color: #ffffff;
        text-decoration: none;
    }

    .bottomLogoContainer{
        width:114px;
        height: 52px;
        margin: auto;
    }

    @media (min-width: 700px) {
        .container{
            width: 680px;
            grid-template-columns: 45% 45%;
            grid-template-rows: 155px;
            grid-gap: 10%;
        }

        .linkSite{
            display: grid;
            grid-template-columns: 30% 30% 30%; 
            grid-template-rows: 20px 20px 20px 20px;
            grid-gap: 5%;
        }

        .bottomLine{
            display: grid;
            grid-template-columns: 60% 35%;
            grid-gap: 5%;
        }

        .newsletterBtn{
            width: 100%;
            margin: auto;
        }
    }

    @media (min-width: 1000px) {
        .container{
            width: 980px;
        }
    }
`;





