import React from 'react';
import styled from 'styled-components';
import Headers from '../components/headers';
import tree from '../images/apply/icono_aplly3.png';

const Apply = ({data, handleClick}) => (
    <ApplyWrapper>    
          <Headers titleBlue="Apply " titleRed="Now" idTag="apply" />
          <div className="applyCards">
            
                <div className="applyCard">
                    <div className="applyCardTitle blue">One</div>
                    <div className="applyCardBody">
                        <div className="applyCardDesc" dangerouslySetInnerHTML={{__html: data.cards[0].text}}></div>
                    </div>
                </div>
                <div className="applyCard">
                    <div className="applyCardTitle red">Two</div>
                    <div className="applyCardBody">
                        <div className="applyCardDesc" dangerouslySetInnerHTML={{__html: data.cards[1].text}}></div>
                        <button className="applyCardBtn" onClick={handleClick} disabled={true}>Here</button>
                    </div>
                </div>
                <div className="applyCard">
                    <div className="applyCardTitle blue">Three</div>
                    <div className="applyCardBody">
                        <div className="applyCardDesc" dangerouslySetInnerHTML={{__html: data.cards[2].text}}></div>
                        <div className="applyCarImg"><img src={tree} alt="Finalist"/></div>
                    </div>
                </div>
                <div className="applyCard">
                    <div className="applyCardTitle red">Four</div>
                    <div className="applyCardBody">
                        <div className="applyCardDesc" dangerouslySetInnerHTML={{__html: data.cards[3].text}}></div>
                    </div>
                </div>
                <div className="applyCard">
                    <div className="applyCardTitle blue">Five</div>
                    <div className="applyCardBody">
                        <div className="applyCardDesc" dangerouslySetInnerHTML={{__html: data.cards[4].text}}></div>
                    </div>
                </div>
                <div className="applyCard">
                    <div className="applyCardTitle red">Six</div>
                    <div className="applyCardBody">
                        <div className="applyCardDesc" dangerouslySetInnerHTML={{__html: data.cards[5].text}}></div>
                    </div>
                </div>
            
        </div>
    </ApplyWrapper>
);

export default Apply;

const ApplyWrapper = styled.div` 
    margin-bottom: 80px;

    .applyCards{
        width: 90%;
        margin: auto;
        text-align: center;
    }

    .applyCard{
        background-color: #ffffff;
        padding: 24px;
        margin: 0px 0px 15px;
    }

    .applyCardTitle{
        font-family: 'Bebas Neue', sans-serif;
        font-size: 24px;
        margin-bottom: 20px;
    }

    .applyCardBody{
        font-family: 'Gotham Condensed', sans-serif;
        color: #4E4E4E;
        font-size: 18px;
    }

    .applyCardBtn{
        width: 80%;
        margin: 40px auto 15px;
        background-color: #B79257;
        color: #ffffff;
        padding: 15px 0px;
        cursor: pointer;
    }
    .applyCarImg{
        max-width: 100%;
        height: auto;
        margin: 40px auto 15px;
    }

    @media (min-width: 700px) {
        .applyCards{            
            width: 680px;
            display: grid;
            grid-template-columns: 30% 30% 30%;
            grid-template-rows: 440px 235px;
            grid-gap: 40px 5%;
        }
        .applyCardBtn{
            margin: 140px auto 0px;
        }
        .applyCarImg{
            margin: 100px auto 0px;
        }
        .applyCard{
            margin: 0px;
        }
    }

    @media (min-width: 1000px) {
        .applyCards{
            width: 905px;
            grid-template-columns: 30% 30% 30%;
            grid-template-rows: 340px 200px;
        }
        .applyCardBtn{
            margin: 100px auto 0px;
        }
        .applyCarImg{
            margin: 70px auto 0px;
        }
    }
`;





