import React from 'react';
import styled from 'styled-components';

const Cover = ({backgroundImage, whiteText, redText, redBig}) => (
    <CoverWrapper>
        <img className='imgResponsive coverImg' src={backgroundImage} alt={whiteText}/>
        <div className='coverText'>
            <div className={'coverTextContainer ' + (redBig?'redBig':'')}>
                <div className='whiteText'>{whiteText}</div>
                <div className={'redText ' + (redBig?'big':'')}>{redText}</div>
            </div>
        </div>
    </CoverWrapper>
);

export default Cover;

const CoverWrapper = styled.div` 
    position: relative;

    .coverImg{
        width: 100%;
    }

    .coverText{
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0px;
        display: grid;
        justify-content:center;
        align-content: center;
        display: grid;
        grid-template-columns: auto;
        grid-template-rows: auto;
    }

    .coverTextContainer {
        line-height: 35px;
        font-size: 40px;
    }

    .redBig{
        line-height: 52px;
    }

    .coverText div{
        text-align: center;
        text-transform: uppercase;
        font-family: 'Bebas Neue', sans-serif;        
    }

    .whiteText{
        color: #ffffff;        
    }

    .redText{
        color: #E20613;        
    }

    .big{
        font-size: 78px;
    }

    @media (min-width: 700px) {
        .coverTextContainer {
            line-height: 75px;
            font-size: 85px;
        }

        .big{
            font-size: 170px;
        }

        .redBig{
            line-height: 105px;
        }
    }

    @media (min-width: 1190px) {
        .coverTextContainer {
            line-height: 90px;
            font-size: 85px;
        }

        .big{
            font-size: 290px;
        }

        .redBig{
            line-height: 210px;
        }
    }
`;





