import React from 'react';
import styled from 'styled-components';
import arrow from '../images/general/triangle_image.png';

const ImageDescription = ({data, image, overImg, isArrowLeft}) => (
    <ImageDescriptionWrapper>    
        <div className="container">
            {isArrowLeft?<div className="textContainer" dangerouslySetInnerHTML={{__html: data.text}}></div>:null}
            <div className="imageContainer">
                <img className="imageDes" src={image} alt={data.image} />
                {undefined!==overImg?<img className="imageOver" src={overImg} alt="Over" />:null}
                <img className={"arrowPos " + (isArrowLeft?"left":"right")} src={arrow} alt="Arrow" />
            </div>
            {!isArrowLeft?<div className="textContainer" dangerouslySetInnerHTML={{__html: data.text}}></div>:null}     
        </div>   
    </ImageDescriptionWrapper>
);

export default ImageDescription;

const ImageDescriptionWrapper = styled.div` 
    .container{
        width: 90%;
        margin: auto;
    }   

    .textContainer{
        background-color: #ffffff;
        padding: 15px 30px;
        font-family: 'Gotham Condensed', sans-serif;
        font-size: 18px;
        color: #4E4E4E;
    }

    .title{
        text-transform: uppercase;
        font-size: 22px;
    }

    .red{
        color: #E20613;
    }

    .imageContainer{
        position: relative;
        overflow: hidden;
        background-color: #000000;
    }

    .imageDes{
        max-width: 100%;
        height: auto;
        margin: auto;
    }

    .arrowPos{
        display: none;
    }

    .imageOver{
        max-width: 100%;
        max-height: 80%;
        margin: auto;
        position: absolute;
        top: 0px;
        left: 0px;
        right: 0px;
        bottom: 0px;
    }

    @media (min-width: 700px) {
        .container{
            width: 680px;
            display: grid;
            grid-template-columns: 50% 50%;
            margin: auto;
        }   
        .imageContainer{
            position: relative;
            overflow: hidden;
        }

        .imageDes{
            max-height: 100%;
            width: auto;
            position: absolute;
            left: -50%;
            right: -50%;
            max-width: none;
        }

        .arrowPos{
            display: block;
            position: absolute;
            top:0px;
            bottom: 0px;
            margin: auto;
        }

        .left{
            left: 0px;
        }

        .right{
            right: 0px;
            -webkit-transform: scaleX(-1);
            transform: scaleX(-1);
        }
    }

     @media (min-width: 1000px) {
        .container{
            width: 905px;
        }
     }
`;





