import React from 'react';
import styled from 'styled-components';
import ImageDescription from '../components/imageDescription';
import Header from '../components/headers';
import { Card, CardImg, CardText, CardBody,
    CardTitle } from 'reactstrap';
import imagePresentation from '../images/seccion1/presentation.jpg';
import imageCard1 from '../images/seccion1/foto_categoria1.jpg';
import imageCard2 from '../images/seccion1/foto_categoria2.jpg';
import imageCard3 from '../images/seccion1/foto_categoria3.jpg';
  

const Section1 = ({data}) => (
    <Section1Wrapper>
        <Header titleBlue="1. Presentation" idTag="presentation"/>
        <ImageDescription data={data.images[0]} image={imagePresentation} isArrowLeft={true}/>
        <Header titleBlue="Categories" idTag="categories" />
        <div className="cards">
            <Card>
                <CardImg top width="100%" src={imageCard1} alt="Sport Performance" />
                <CardBody>
                    <CardTitle>Sport <span className="red">Performance</span></CardTitle>
                    <CardText>
                        2018 theme: Innovation in training systems, tools and services leading players to higher performance 
                    </CardText>
                </CardBody>
            </Card>
            <Card>
                <CardImg top width="100%" src={imageCard3} alt="Sport Performance" />
                <CardBody>
                    <CardTitle>Protecting <span className="red">Player</span></CardTitle>
                    <CardText>
                        2018 theme: Preventing injuries and helping players recover faster
                    </CardText>
                </CardBody>
            </Card>
            <Card>
                <CardImg top width="100%" src={imageCard2} alt="Sport Performance" />
                <CardBody>
                    <CardTitle>Fan <span className="red">Engagement</span></CardTitle>
                    <CardText>
                        2018 theme: Place the fan at the core of the game
                    </CardText>
                </CardBody>
            </Card>
        </div>
    </Section1Wrapper>
);

export default Section1;

const Section1Wrapper = styled.div` 
    .cards{
        width: 90%;
        margin: auto;     
    }

    .card{
       background-color: #ffffff;
       text-align: center;
       margin-bottom: 15px;
    }

    .card-title{
        color: #004586;
        font-family: 'Bebas Neue', sans-serif;
        font-size: 28px;
    }

    .card-body{
        font-family: 'Gotham Condensed', sans-serif;
        font-size: 18px;
        color: #4E4E4E;
        padding: 0px 12px 30px;
    }

    @media (min-width: 700px) {
        .cards{
            width: 680px;  
            display: grid;
            grid-template-columns: 30% 30% 30%;
            grid-gap: 5%;                
        }
    }

     @media (min-width: 1000px) {
        .cards{
            width: 905px;
        }
     }
`;





