import React from 'react';
import styled from 'styled-components';

const Headers = ({titleBlue, titleRed, subtitle, idTag}) => (
    <HeadersWrapper>    
        <div id={idTag} className="title">
            {titleBlue} 
            {undefined !== titleRed ? <span className="red">{titleRed}</span> : null}
        </div>
        {undefined !== subtitle ? <div className="subtitle">{subtitle}</div> : null}
    </HeadersWrapper>
);

export default Headers;

const HeadersWrapper = styled.div` 
    padding-top: 45px;
    padding-bottom: 45px;
    text-align: center;

    .title{
        color: #004586;
        font-family: 'Bebas Neue', sans-serif;
        font-size: 50px;
        text-transform: uppercase;
    }

    .red{
        color: #E20613;
    }

    .subtitle{
        font-family: 'Gotham Condensed', sans-serif;
        color: #4E4E4E;
        font-size: 25px;
        padding-top: 20px;
        text-align: center;
        max-width: 680px;
        margin: auto;
    }

    @media (min-width: 700px) {
        .title{
            font-size: 70px;
        }
    }
`;





