import React from 'react';
import styled from 'styled-components';
import fffLogo from '../images/header/logo_ff.svg';
import sisLogo from '../images/header/logo_sis.png';

const Header = () => (
    <HeaderWrapper>
        <div className="container">
            <div className="topLine"></div>
            <div className="bottomLine">
                <img className="imgResponsive" src={fffLogo} alt="FFF Logo"/>
                <img id="sisLogo" className="imgResponsive" src={sisLogo} alt="SIS Logo"/>
            </div>
        </div>
    </HeaderWrapper>
);

export default Header;

const HeaderWrapper = styled.div`
    .container{
        height: 115px;
    }

    .topLine{
        background-color: #dfe2e5;
        height: 25px;
        width: 100%;
    }
    .bottomLine{
        width: 100%;
        height: 90px;        
        justify-content:center;
        align-content: center;
        display: grid;
        grid-template-columns: 150px 115px;
        grid-template-rows: 50px;
        grid-gap: 35px;
    }

    #sisLogo{
        align-self: center;
    }

    @media (min-width: 700px) {
        .container{
            height: 205px;
        }
        .topLine{
            height: 50px;
        }
        .bottomLine{      
            height: 155px;      
            grid-template-columns: 295px 159px;
            grid-template-rows: 100px;
            grid-gap: 125px;
        }
    }
    
`;





