import React from 'react';
import styled from 'styled-components';
import ImageDescription from '../components/imageDescription';
import PrizesImg from '../images/prizes/foto_prizes.jpg';
import TrophyImg from '../images/prizes/icono_prizes.png';
import TicketsImg from '../images/prizes/icono_ticketing.png';
import VenueImg from '../images/prizes/icono_venue.png';

const Prizes = ({data}) => (
    <PrizesWrapper>    
        <div id="pizes"/>
        <div className="imageDesc"><ImageDescription data={data.images[0]} image={PrizesImg} overImg={TrophyImg} isArrowLeft={true}/></div>
        <div id="ticketing" className="cards">
            <div className="card">
                <div className="topCard blue-king-bg">
                    <img src={TicketsImg} alt="Tickets" />
                </div>
                <div className="cardBody">
                    <div className="cardBodyTitle blue-king">Attend the awards</div>
                    <a href="http://sis.news"><div className="cardBodyBtn">Go to SiS.news</div></a>
                </div>
            </div>
            <div className="card">
                <div className="topCard blue-light-bg">
                    <img src={VenueImg} alt="Venue" />
                </div>
                <div className="cardBody">
                    <div className="cardBodyTitle blue-light">Venue</div>
                    <div className="cardBodyText">MEDEF Headquarters,55 av. Bosquet 75007, Paris, France</div>
                </div>
            </div>
        </div>
    </PrizesWrapper>
);

export default Prizes;

const PrizesWrapper = styled.div` 
    margin: 80px auto;

    .cards{
        width: 90%;
        margin: 40px auto 0px;
    }
    .card{
        margin-bottom: 30px;
        background-color: #ffffff;
    }

    .topCard{
        width: 100%;
        padding: 15px 0px;
    }

    .topCard img{
        margin: auto;
        max-width: 100%;
        height: auto;
        display: block;
    }
    .cardBody{
        font-family: 'Bebas Neue', sans-serif;
        padding: 20px 30px;
        text-align: center;
        color: #4E4E4E;
    }
    .cardBodyTitle{
        font-size: 30px;
    }
    .cardBodyText{
        font-size: 18px;
        margin: 40px auto 15px;
    }
    .cardBodyBtn{
        width: 80%;
        margin: 40px auto 15px;
        background-color: #E20613;
        color: #ffffff;
        padding: 15px 0px;
        cursor: pointer;
    }
    
    @media (min-width: 700px) {
        .cards{
            width: 680px;
            display: grid;
            grid-template-columns: 45% 45%;
            grid-template-rows: 400px;
            grid-gap: 10%;
        }
        .card{
            margin-bottom: 0px;
            display:grid;
            grid-template-columns: 100%;
            grid-template-rows: 50% 50%;
        }
        .cardBody{
            padding: 20px 80px;
        }
        .cardBodyBtn, .cardBodyText{
            margin: 22px auto 0px;
        }
    }

    @media (min-width: 1000px) {
        .cards{
            width: 905px;
        }
    }
`;





