import React, { Component } from 'react';
import axios from 'axios';
import Data from './data/data.json';
import StartImage from './images/cover/imagen_inicio.jpg';
import DeadlineImage from './images/cover/fondo_deadline.jpg';
import Header from './sections/header';
import Cover from './sections/cover';
import Section1 from './sections/section1';
import Section2 from './sections/section2';
import Apply from './sections/apply';
import Prices from './sections/prices';
import About from './sections/about';
import Footer from './sections/footer';
import Modal from './components/modal';
import qs from "qs";

class App extends Component {
  state = {
    modalVisible: false,
    name: null,
    job:null,
    company: null,
    video: null
  }

  handleChangeName = event => {
    this.setState({ name: event.target.value });
  }
  handleChangeJob = event => {
    this.setState({ job: event.target.value });
  }
  handleChangeCompany = event => {
    this.setState({ company: event.target.value });
  }
  handleChangeVideo = event => {
    this.setState({ video: event.target.value });
  }

  handleModalToggle = (evt) => {
    this.setState({
      modalVisible: !this.state.modalVisible
    });
  }

  handleSubmit = event => {
    console.log("submit");
    event.preventDefault();

    const user = {
      Name: this.state.name,
      JobTitle: this.state.job,
      Company: this.state.company,
      VideoUrl: this.state.video
    };

    axios.post(`/CreateUser.php`,  qs.stringify(user))
      .then(res => {
        console.log(res);
      }).catch((error) => {
        console.log(error);
    });
  }

  render() {
    return (
      <div className="landing">
        {
          this.state.modalVisible &&
          <Modal 
            handleCloseClick={this.handleModalToggle}
            handleSubmit={this.handleSubmit}
            handleChangeName={this.handleChangeName}
            handleChangeJob={this.handleChangeJob}
            handleChangeCompany={this.handleChangeCompany}
            handleChangeVideo={this.handleChangeVideo}
          />
        }        
        <Header />
        <Cover backgroundImage={StartImage} whiteText={'fff innovation'} redText={"awards"} redBig={true}/>
        <Section1 data={Data.section1}/>
        <Section2 data={Data.section2}/>
        <Apply data={Data.apply} handleClick={this.handleModalToggle}/>
        <div id="deadline" />
        <Cover backgroundImage={DeadlineImage} whiteText={'Deadline'} redText={"September 28th, 2018"} redBig={false}/>
        <Prices data={Data.prizes} />
        <About data={Data.about} />
        <Footer />
      </div>
    );
  }
}

export default App;



